import React, { Component } from 'react';
import './App.css';
import Info from './Info';
import Projects from './Projects';
import Contact from './Contact';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
        showInfo: true,
        showProjects: false,
        showContact: false,
    }

    this.toggleInfo = this.toggleInfo.bind(this);
    this.toggleProjects = this.toggleProjects.bind(this);
    this.toggleContact = this.toggleContact.bind(this);
  }

  toggleInfo() {
    this.setState({
      showInfo: true,
      showProjects: false,
      showContact: false
    });
  }

  toggleProjects() {
    this.setState({
      showProjects: true,
      showInfo: false,
      showContact: false
    });
  }

  toggleContact() {
    this.setState({
      showContact: true,
      showProjects: false,
      showInfo: false
    });
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src="face.png" className="App-logo" alt="logo" />
          <div className="links">
            <button className="Site-links" onClick={this.toggleInfo}>Info<img src="info.png" className="Button-logo" alt="logo" /></button>
            <button className="Site-links" onClick={this.toggleProjects}>Projects<img src="info.png" className="Button-logo" alt="logo" /></button>
            <button className="Site-links" onClick={this.toggleContact}>Contact<img src="contact.png" className="Button-logo" alt="logo" /></button>
            <a href='https://github.com/atarvainen' target="_blank" rel="noopener noreferrer"><button className="Social-logo"><img src="github-11-24.png" className="social" alt="GH" /></button></a>
            <a href='https://www.linkedin.com/in/antti-tarvainen/' target="_blank" rel="noopener noreferrer"><button className="Social-logo"><img src="linkedin-3-24.png" className="social" alt="LI" /></button></a>
          </div>
        </header>
        {this.state.showInfo ?
          <Info />            
          : null
        }

        {this.state.showProjects ?
          <Projects />            
          : null
        }

        {this.state.showContact ?
          <Contact />            
          : null
        }
      </div>
    );
  }
}

export default App;
